export const pageQuery = graphql`
  {
    images: allFile(filter: { relativeDirectory: { eq: "about" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              base64
              tracedSVG
              srcWebp
              srcSetWebp
              originalImg
              originalName
            }
          }
        }
      }
    }
  }
`;

